.container {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 50px;
}
.loader {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px solid orange;
    animation: rotate 1s infinite linear;
}

@keyframes rotate {
    from {
        transform: rotate(0deg) scale(1);
    }
    to {
        transform: rotate(360deg) scale(1.4);
    }
}
